table.cart-data{
  border-top:1px solid #eee
}
table.cart-data thead,table.cart-data tbody{
  border-bottom:1px solid #eee
}
table.cart-data tr th,table.cart-data tr td{
  padding:7px 0
}
table.cart-data tr td.expiry-date>div{
  display:inline-block;
  margin-bottom:0
}
table.cart-data tr td.expiry-date .c-spinner{
  position:relative;
  margin-left:1rem;
  top:6px
}
table.cart-data tr td.thin .c-btn{
  white-space:nowrap
}
table.cart-data .item-name .edition-badge{
  margin-left:0.5rem
}
table.cart-data .plugin-icon{
  margin-right:10px !important
}
table.cart-data .plugin-icon img{
  max-width:none
}
@media(max-width: 991px){
table.cart-data thead{
    display:none
}
table.cart-data tr,table.cart-data td,table.cart-data th{
    display:block
}
table.cart-data tr.sub-item td.blank-cell,table.cart-data tr.sub-item td.empty-cell{
    display:none
}
}
@media(min-width: 992px){
table.cart-data tr.sub-item td:not(.blank-cell){
    border-top:1px dotted #eee
}
table.cart-data tr th,table.cart-data tr td{
    padding:10px 0
}
table.cart-data tr th.price,table.cart-data tr td.price{
    text-align:right
}
table.cart-data tr th.total-price,table.cart-data tr td.total-price{
    text-align:right
}
table.cart-data tr td.expiry-date{
    width:60%
}
}