.plugin-icon{
  position:relative
}
body.ltr .plugin-icon{
  margin-right:1.5rem
}
body.rtl .plugin-icon{
  margin-left:1.5rem
}
ul.plugin-meta{
  margin-left:-1rem;
  margin-right:-1rem;
  display:-webkit-flex;
  display:flex;
  -webkit-flex-wrap:wrap;
          flex-wrap:wrap
}
ul.plugin-meta li{
  margin-bottom:2rem;
  -webkit-flex-shrink:0;
          flex-shrink:0;
  -webkit-flex-grow:0;
          flex-grow:0;
  padding-left:1rem;
  padding-right:1rem;
  -webkit-flex-basis:50%;
          flex-basis:50%
}
ul.plugin-meta li span{
  display:block;
  --tw-text-opacity:1;
  color:rgba(113, 113, 122, var(--tw-text-opacity))
}
@media only screen and (min-width: 672px){
ul.plugin-meta li{
    -webkit-flex-basis:33.3333%;
            flex-basis:33.3333%
}
}
@media only screen and (min-width: 1400px){
ul.plugin-meta li{
    -webkit-flex-basis:25%;
            flex-basis:25%
}
}
@media only screen and (min-width: 1824px){
ul.plugin-meta li{
    -webkit-flex-basis:20%;
            flex-basis:20%
}
}