.plugin-editions-edition{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  border-radius: 0.25rem;
  border-width: 1px;
  border-style: solid;
  --tw-border-opacity: 1;
  border-color: rgba(228, 228, 231, var(--tw-border-opacity));
  padding: 2rem;
  text-align: center
}
.plugin-editions-edition .description{
  -webkit-flex: 1 1 0%;
          flex: 1 1 0%
}
.plugin-editions-edition .description .edition-name{
  display: inline-block;
  border-bottom-width: 1px;
  border-style: solid;
  --tw-border-opacity: 1;
  border-color: rgba(228, 228, 231, var(--tw-border-opacity));
  padding-top: 0.25rem;
  padding-bottom: 0.25rem;
  font-size: 1.125rem;
  line-height: 1.75rem;
  font-weight: 700;
  text-transform: uppercase;
  --tw-text-opacity: 1;
  color: rgba(82, 82, 91, var(--tw-text-opacity))
}
.plugin-editions-edition .description .price{
  margin-top: 2rem;
  margin-bottom: 2rem;
  font-size: 1.875rem;
  line-height: 2.25rem;
  font-weight: 700
}
.plugin-editions-edition .description ul{
  margin-bottom: 2rem;
  text-align: left
}
.plugin-editions-edition .description ul li{
  border-bottom-width: 1px;
  border-style: solid;
  --tw-border-opacity: 1;
  border-color: rgba(212, 212, 216, var(--tw-border-opacity));
  padding-top: 0.5rem;
  padding-bottom: 0.5rem
}
.plugin-editions-edition .description ul li:first-child{
  border-top-width: 1px
}