.c-btn:focus, a.c-btn:focus, button.c-btn:focus{
  --tw-shadow:0 0 0 3px var(--craftui-global-shadow-outline);
  box-shadow:var(--tw-ring-offset-shadow, 0 0 #0000), var(--tw-ring-shadow, 0 0 #0000), var(--tw-shadow);
  outline:2px solid transparent;
  outline-offset:2px
}
.c-btn.block, a.c-btn.block, button.c-btn.block{
  margin-top:0.5rem;
  margin-bottom:0.5rem;
  width:100%
}
.c-btn.small, a.c-btn.small, button.c-btn.small{
  padding-left:0.75rem;
  padding-right:0.75rem;
  line-height:1rem
}
.c-btn.small .c-icon,a.c-btn.small .c-icon,button.c-btn.small .c-icon{
  width:12px;
  height:12px
}
.c-btn.large, a.c-btn.large, button.c-btn.large{
  font-size:1rem;
  line-height:1.5rem;
  line-height:1.5rem
}
.c-btn.outline .c-icon, a.c-btn.outline .c-icon, button.c-btn.outline .c-icon{
  fill:currentColor
}
.c-btn.loading, a.c-btn.loading, button.c-btn.loading{
  position:relative
}
.c-btn.loading .c-spinner, a.c-btn.loading .c-spinner, button.c-btn.loading .c-spinner{
  position:absolute;
  top:0;
  right:0;
  bottom:0;
  left:0;
  display:-webkit-flex;
  display:flex;
  -webkit-align-items:center;
          align-items:center;
  -webkit-justify-content:center;
          justify-content:center
}
.c-btn.loading .c-btn-content, a.c-btn.loading .c-btn-content, button.c-btn.loading .c-btn-content{
  visibility:hidden
}
.c-btn .c-icon, a.c-btn .c-icon, button.c-btn .c-icon{
  vertical-align:middle
}
.c-btn .c-btn-content, a.c-btn .c-btn-content, button.c-btn .c-btn-content{
  display:inline-block
}