.info-hud .v-popover{
  --tw-text-opacity:1;
  color:rgba(161, 161, 170, var(--tw-text-opacity));
  display:inline-block;
  line-height:0
}
.info-hud .v-popover:hover, .info-hud .v-popover.open{
  cursor:pointer;
  --tw-text-opacity:1;
  color:rgba(59, 130, 246, var(--tw-text-opacity))
}
.info-hud .v-popover>span{
  outline:none
}
.tooltip{
  max-width:250px
}