html{
  box-sizing:border-box
}
*,:after,:before{
  box-sizing:inherit
}
img{
  height:auto;
  max-width:100%
}
#main-container #main #main-content #content-container #content{
  display:-webkit-flex;
  display:flex;
  padding:0;
  min-height:400px
}
.ps-wrapper{
  display:-webkit-flex;
  display:flex;
  -webkit-flex:1 1 0%;
          flex:1 1 0%;
  -webkit-flex-direction:column;
          flex-direction:column;
  padding-bottom:0 !important
}
.ps-wrapper .ps-sidebar{
  width:100%;
  padding-left:1.5rem;
  padding-right:1.5rem;
  padding-top:1.5rem;
  padding-bottom:1.5rem;
  background-color:#f3f7fc
}
body.ltr .ps-wrapper .ps-sidebar{
  border-radius:5px 0 0 5px
}
body.rtl .ps-wrapper .ps-sidebar{
  border-radius:0 5px 5px 0
}
.ps-wrapper .ps-main{
  display:-webkit-flex;
  display:flex;
  -webkit-flex:1 1 0%;
          flex:1 1 0%;
  overflow:auto
}
.ps-wrapper .ps-main .ps-container{
  margin-left:auto;
  margin-right:auto;
  max-width:1280px;
  -webkit-flex:1 1 0%;
          flex:1 1 0%
}
.ps-container{
  padding-left:2.5rem;
  padding-right:2.5rem;
  padding-top:1.5rem;
  padding-bottom:1.5rem
}
@media only screen and (min-width: 975px){
.ps-wrapper{
    -webkit-flex-direction:row;
            flex-direction:row
}
.ps-wrapper .ps-sidebar{
    width:15rem;
    overflow:auto;
    border-right-width:1px;
    border-bottom-width:0px
}
.ps-wrapper .ps-sidebar .categories{
    display:block
}
}
.ps-grid-plugins{
  margin-left:-1rem;
  margin-right:-1rem;
  display:-webkit-flex;
  display:flex;
  -webkit-flex-wrap:wrap;
          flex-wrap:wrap;
  box-sizing:border-box
}
.ps-grid-plugins .ps-grid-box{
  width:100%;
  box-sizing:border-box
}
.ps-grid-plugins .ps-grid-box .plugin-card{
  margin-left:1rem;
  margin-right:1rem
}
.ps-grid-plugins .ps-grid-box .plugin-card .plugin-icon{
  -webkit-flex-shrink:0;
          flex-shrink:0
}
@media only screen and (min-width: 600px){
.ps-grid-plugins .ps-grid-box{
    width:50%
}
}
@media only screen and (min-width: 1400px){
.ps-grid-plugins .ps-grid-box{
    width:33.333333%
}
}
@media only screen and (min-width: 1824px){
.ps-grid-plugins .ps-grid-box{
    width:25%
}
}
html,body,div,span,object,iframe,table,caption,tbody,tfoot,thead,tr,th,td,del,dfn,em,font,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,article,main,nav,ins{
  min-width:0
}
hr{
  --tw-border-opacity:1;
  border-color:rgba(244, 244, 245, var(--tw-border-opacity))
}
h1{
  margin-bottom:0;
  font-size:18px
}
h2{
  margin-top:2rem;
  margin-bottom:1rem
}
p{
  margin-bottom:1rem
}
pre{
  margin-bottom:1rem;
  min-width:0px;
  max-width:100%;
  overflow:auto;
  --tw-bg-opacity:1;
  background-color:rgba(212, 212, 216, var(--tw-bg-opacity));
  padding:1rem
}
code{
  --tw-bg-opacity:1;
  background-color:rgba(212, 212, 216, var(--tw-bg-opacity));
  padding:0.25rem
}
.no-line-height{
  line-height:0
}
.input.success input{
  border-color:#27ab83
}
.input.errors .select:not(.selectize) select{
  box-shadow:inset 0 0 0 1px transparent,0 0 0 1px #cf1124,0 1px 1px rgba(0,0,0,.1)
}
input.error{
  --tw-border-opacity:1;
  border-color:rgba(239, 68, 68, var(--tw-border-opacity))
}
.ps-header{
  display:-webkit-flex;
  display:flex;
  -webkit-justify-content:space-between;
          justify-content:space-between;
  border-bottom-width:1px;
  border-style:solid;
  --tw-border-opacity:1;
  border-color:rgba(161, 161, 170, var(--tw-border-opacity));
  padding-top:0.5rem;
  padding-bottom:0.5rem
}
.swiper-wrapper{
  min-width:auto
}
#header h1{
  cursor:pointer
}
#main-container{
  position:relative
}
#main-content #content{
  position:relative
}
#container{
  position:static !important
}
#pluginstore-actions{
  display:-webkit-flex;
  display:flex;
  -webkit-align-items:center;
          align-items:center;
  -webkit-justify-content:space-between;
          justify-content:space-between
}
body.ltr #pluginstore-actions>*{
  margin-left:18px
}
body.rtl #pluginstore-actions>*{
  margin-right:18px
}
#pluginstore-actions #cart-button svg{
  display:inline-block;
  width:18px;
  height:18px;
  vertical-align:middle;
  line-height:0;
  margin-right:3px
}
#pluginstore-actions #cart-button svg rect,#pluginstore-actions #cart-button svg circle,#pluginstore-actions #cart-button svg ellipse,#pluginstore-actions #cart-button svg line,#pluginstore-actions #cart-button svg polyline,#pluginstore-actions #cart-button svg polygon,#pluginstore-actions #cart-button svg path,#pluginstore-actions #cart-button svg text{
  fill:#606d7b;
  stroke-width:0;
  -o-transition:fill linear 100ms;
  transition:fill linear 100ms
}
#pluginstore-actions #cart-button:hover svg rect,#pluginstore-actions #cart-button:hover svg circle,#pluginstore-actions #cart-button:hover svg ellipse,#pluginstore-actions #cart-button:hover svg line,#pluginstore-actions #cart-button:hover svg polyline,#pluginstore-actions #cart-button:hover svg polygon,#pluginstore-actions #cart-button:hover svg path,#pluginstore-actions #cart-button:hover svg text{
  fill:#55616d;
  stroke-width:0;
  -o-transition:fill linear 100ms;
  transition:fill linear 100ms
}
#pluginstore-actions #cart-button .badge{
  position:relative;
  display:inline-block;
  -webkit-flex-shrink:1;
          flex-shrink:1;
  --tw-text-opacity:1;
  color:rgba(255, 255, 255, var(--tw-text-opacity));
  top:-1px;
  padding:0 6px;
  font-size:11px;
  line-height:16px;
  border-radius:1em;
  background:#e12d39
}
body.ltr #pluginstore-actions #cart-button .badge{
  margin:0 0 0 -6px
}
body.rtl #pluginstore-actions #cart-button .badge{
  margin:0 -6px 0 0
}
.readable table{
  display:block;
  overflow:auto;
  width:100%
}
.readable table th{
  font-weight:600
}
.readable table td,.readable table th{
  border:1px solid #dfe2e5;
  padding:6px 13px
}
.readable table tr{
  background-color:#fff;
  border-top:1px solid #c6cbd1
}
.readable table tr:nth-child(2n){
  background-color:#f3f7fc
}
.readable table img{
  background-color:transparent
}
ul.categories{
  margin-left:-1.5rem;
  margin-right:-1.5rem
}
ul.categories li{
  position:relative
}
ul.categories li:before, ul.categories li:last-child:after{
  position:absolute;
  right:0;
  left:0
}
ul.categories li:before,ul.categories li:last-child:after{
  content:""
}
ul.categories li:before{
  top:0
}
ul.categories li:last-child:after{
  bottom:0
}
ul.categories li a{
  position:relative;
  display:block;
  padding-left:1.5rem;
  padding-right:1.5rem;
  padding-top:0.5rem;
  padding-bottom:0.5rem;
  --tw-text-opacity:1;
  color:rgba(39, 39, 42, var(--tw-text-opacity))
}
ul.categories li a img{
  margin-right:0.5rem;
  vertical-align:middle;
  width:24px
}
ul.categories li a:hover{
  z-index:10;
  text-decoration:none;
  background-color:#e4edf6
}
ul.categories li a:hover:not(.router-link-active){
  color:#0b69a3
}
ul.categories li a.router-link-active{
  background-color:#cdd8e4
}
.tooltip{
  display:block !important;
  z-index:10000;
  background:#fff;
  box-shadow:0 25px 100px rgba(0,0,0,.5);
  padding:24px
}
.tooltip .tooltip-arrow{
  width:0;
  height:0;
  border-style:solid;
  position:absolute;
  margin:12px;
  border-color:#fff
}
.tooltip[x-placement^=top]{
  margin-bottom:12px
}
.tooltip[x-placement^=top] .tooltip-arrow{
  border-width:12px 12px 0 12px;
  border-left-color:transparent !important;
  border-right-color:transparent !important;
  border-bottom-color:transparent !important;
  bottom:-12px;
  left:calc(50% - 12px);
  margin-top:0;
  margin-bottom:0
}
.tooltip[x-placement^=bottom]{
  margin-top:12px
}
.tooltip[x-placement^=bottom] .tooltip-arrow{
  border-width:0 12px 12px 12px;
  border-left-color:transparent !important;
  border-right-color:transparent !important;
  border-top-color:transparent !important;
  top:-12px;
  left:calc(50% - 12px);
  margin-top:0;
  margin-bottom:0
}
.tooltip[x-placement^=right]{
  margin-left:12px
}
.tooltip[x-placement^=right] .tooltip-arrow{
  border-width:12px 12px 12px 0;
  border-left-color:transparent !important;
  border-top-color:transparent !important;
  border-bottom-color:transparent !important;
  left:-12px;
  top:calc(50% - 12px);
  margin-left:0;
  margin-right:0
}
.tooltip[x-placement^=left]{
  margin-right:12px
}
.tooltip[x-placement^=left] .tooltip-arrow{
  border-width:12px 0 12px 12px;
  border-top-color:transparent !important;
  border-right-color:transparent !important;
  border-bottom-color:transparent !important;
  right:-12px;
  top:calc(50% - 12px);
  margin-left:0;
  margin-right:0
}
.tooltip[aria-hidden=true]{
  visibility:hidden;
  opacity:0;
  -o-transition:opacity .15s,visibility .15s;
  transition:opacity .15s,visibility .15s
}
.tooltip[aria-hidden=false]{
  visibility:visible;
  opacity:1;
  -o-transition:opacity .15s;
  transition:opacity .15s
}
.c-btn.primary:not(.outline), a.c-btn.primary:not(.outline), button.c-btn.primary:not(.outline){
  --tw-border-opacity:1;
  border-color:rgba(239, 68, 68, var(--tw-border-opacity));
  --tw-bg-opacity:1;
  background-color:rgba(239, 68, 68, var(--tw-bg-opacity));
  --tw-text-opacity:1;
  color:rgba(255, 255, 255, var(--tw-text-opacity))
}
.c-btn.primary:not(.outline):not([disabled]):hover, a.c-btn.primary:not(.outline):not([disabled]):hover, button.c-btn.primary:not(.outline):not([disabled]):hover{
  --tw-border-opacity:1;
  border-color:rgba(220, 38, 38, var(--tw-border-opacity));
  --tw-bg-opacity:1;
  background-color:rgba(220, 38, 38, var(--tw-bg-opacity))
}
.c-btn.primary:not(.outline):not([disabled]):active, a.c-btn.primary:not(.outline):not([disabled]):active, button.c-btn.primary:not(.outline):not([disabled]):active{
  --tw-border-opacity:1;
  border-color:rgba(185, 28, 28, var(--tw-border-opacity));
  --tw-bg-opacity:1;
  background-color:rgba(185, 28, 28, var(--tw-bg-opacity))
}
.c-btn.outline.primary, a.c-btn.outline.primary, button.c-btn.outline.primary{
  --tw-border-opacity:1;
  border-color:rgba(239, 68, 68, var(--tw-border-opacity));
  --tw-text-opacity:1;
  color:rgba(239, 68, 68, var(--tw-text-opacity))
}
.c-btn.outline.primary:not([disabled]):hover, a.c-btn.outline.primary:not([disabled]):hover, button.c-btn.outline.primary:not([disabled]):hover{
  --tw-bg-opacity:1;
  background-color:rgba(239, 68, 68, var(--tw-bg-opacity));
  --tw-text-opacity:1;
  color:rgba(255, 255, 255, var(--tw-text-opacity))
}
.c-btn.outline.primary:not([disabled]):active, a.c-btn.outline.primary:not([disabled]):active, button.c-btn.outline.primary:not([disabled]):active{
  --tw-bg-opacity:1;
  background-color:rgba(185, 28, 28, var(--tw-bg-opacity));
  --tw-text-opacity:1;
  color:rgba(255, 255, 255, var(--tw-text-opacity))
}
.c-btn.loading.outline.primary .c-spinner>.animation, a.c-btn.loading.outline.primary .c-spinner>.animation, button.c-btn.loading.outline.primary .c-spinner>.animation{
  --tw-border-opacity:1;
  border-color:rgba(239, 68, 68, var(--tw-border-opacity))
}
.c-btn.loading.outline.danger .c-spinner>.animation, a.c-btn.loading.outline.danger .c-spinner>.animation, button.c-btn.loading.outline.danger .c-spinner>.animation{
  --tw-border-opacity:1;
  border-color:rgba(239, 68, 68, var(--tw-border-opacity))
}