.changelog-release{
  border-bottom-width:1px;
  border-style:solid;
  --tw-border-opacity:1;
  border-color:rgba(228, 228, 231, var(--tw-border-opacity));
  padding-top:0.5rem;
  padding-bottom:1rem
}
.changelog-release .version{
  position:relative
}
.changelog-release .version .anchor{
  position:absolute;
  border-radius:9999px;
  padding:0.25rem;
  --tw-text-opacity:1;
  color:rgba(255, 255, 255, var(--tw-text-opacity));
  top:0px;
  font-size:14px;
  -webkit-transform:rotate(45deg);
       -o-transform:rotate(45deg);
          transform:rotate(45deg)
}
body.ltr .changelog-release .version .anchor{
  left:-24px
}
body.rtl .changelog-release .version .anchor{
  right:-24px
}
.changelog-release .version .anchor:hover{
  --tw-text-opacity:1;
  color:rgba(0, 0, 0, var(--tw-text-opacity))
}
.changelog-release .version:hover .anchor{
  --tw-text-opacity:1;
  color:rgba(0, 0, 0, var(--tw-text-opacity))
}
.changelog-release .version h2{
  margin-top:1.5rem;
  margin-bottom:0.5rem
}
.changelog-release .version .date{
  --tw-text-opacity:1;
  color:rgba(113, 113, 122, var(--tw-text-opacity))
}
.changelog-release .version .critical{
  margin-top:0.5rem;
  display:inline-block;
  border-radius:0.25rem;
  border-width:1px;
  border-style:solid;
  --tw-border-opacity:1;
  border-color:rgba(239, 68, 68, var(--tw-border-opacity));
  padding-left:0.25rem;
  padding-right:0.25rem;
  padding-top:0;
  padding-bottom:0;
  font-size:0.875rem;
  line-height:1.25rem;
  text-transform:uppercase;
  --tw-text-opacity:1;
  color:rgba(239, 68, 68, var(--tw-text-opacity))
}
.changelog-release .details{
  padding-top:1.5rem
}
.changelog-release .details h3{
  margin-top:1.5rem;
  margin-bottom:1rem
}
.changelog-release .details ul{
  margin-bottom:1rem;
  margin-left:1.5rem;
  line-height:1.5;
  list-style-type:disc
}
.changelog-release .details ul li:not(:first-child){
  margin-top:0.25rem
}
@media(min-width: 992px){
.changelog-release{
    display:-webkit-flex;
    display:flex
}
.changelog-release .version{
    width:100%;
    max-width:20rem
}
.changelog-release .version .anchor{
    top:20px
}
.changelog-release .details{
    -webkit-flex:1 1 0%;
            flex:1 1 0%
}
}