#pluginstore-modal{
  position:absolute;
  top:0;
  left:0;
  max-width:850px;
  max-height:650px;
  z-index:20000
}
#pluginstore-modal .pluginstore-modal-flex{
  position:absolute;
  top:0;
  right:0;
  bottom:0;
  left:0;
  display:-webkit-flex;
  display:flex;
  -webkit-flex-direction:column;
          flex-direction:column
}
#pluginstore-modal .pluginstore-modal-flex header .btn-left{
  position:absolute;
  top:28px
}
body.ltr #pluginstore-modal .pluginstore-modal-flex header .btn-left{
  left:24px
}
body.rtl #pluginstore-modal .pluginstore-modal-flex header .btn-left{
  right:24px
}
#pluginstore-modal .pluginstore-modal-flex header h1{
  text-align:center
}
#pluginstore-modal .pluginstore-modal-flex .pluginstore-modal-main{
  position:relative;
  margin-bottom:0;
  display:-webkit-flex;
  display:flex;
  min-height:0px;
  -webkit-flex-grow:1;
          flex-grow:1
}
#pluginstore-modal .pluginstore-modal-flex .pluginstore-modal-main .pluginstore-modal-content{
  -webkit-flex-grow:1;
          flex-grow:1;
  overflow:auto;
  padding:24px
}