#screenshot-modal{
  position:fixed;
  top:0;
  right:0;
  bottom:0;
  left:0;
  overflow:hidden;
  --tw-bg-opacity:1;
  background-color:rgba(250, 250, 250, var(--tw-bg-opacity));
  z-index:101
}
#screenshot-modal .close{
  position:absolute;
  top:0;
  left:0;
  z-index:30;
  display:inline-block;
  text-align:center;
  font-size:30px;
  color:rgba(0,0,0,.6);
  padding:14px 24px;
  line-height:16px
}
#screenshot-modal .close:hover{
  text-decoration:none;
  color:rgba(0,0,0,.8)
}
#screenshot-modal .carousel{
  position:absolute;
  top:0;
  right:0;
  bottom:0;
  left:0;
  display:-webkit-flex;
  display:flex
}
#screenshot-modal .carousel .swiper-container{
  display:-webkit-flex;
  display:flex
}
#screenshot-modal .carousel .swiper-container .swiper-wrapper{
  display:-webkit-flex;
  display:flex;
  height:auto;
  width:auto;
  -webkit-flex:1 1 0%;
          flex:1 1 0%
}
#screenshot-modal .carousel .swiper-container .swiper-wrapper .swiper-slide{
  display:-webkit-flex;
  display:flex;
  -webkit-flex:1 1 0%;
          flex:1 1 0%;
  -webkit-align-items:center;
          align-items:center;
  -webkit-justify-content:center;
          justify-content:center;
  text-align:center
}
#screenshot-modal .carousel .swiper-container .swiper-wrapper .swiper-slide .screenshot{
  display:-webkit-flex;
  display:flex;
  height:100%;
  -webkit-flex:1 1 0%;
          flex:1 1 0%;
  -webkit-align-items:center;
          align-items:center;
  -webkit-justify-content:center;
          justify-content:center;
  box-sizing:border-box
}
#screenshot-modal .carousel .swiper-container .swiper-wrapper .swiper-slide .screenshot .swiper-zoom-container{
  display:-webkit-flex;
  display:flex;
  height:100%;
  width:100%;
  -webkit-align-items:center;
          align-items:center;
  -webkit-justify-content:center;
          justify-content:center;
  text-align:center
}
#screenshot-modal .carousel .swiper-container .swiper-wrapper .swiper-slide .screenshot .swiper-zoom-container img{
  max-height:100%;
  max-width:100%
}
#screenshot-modal .carousel .swiper-button-prev, #screenshot-modal .carousel .swiper-button-next{
  display:-webkit-flex;
  display:flex;
  width:auto;
  -webkit-align-items:center;
          align-items:center;
  -webkit-justify-content:center;
          justify-content:center
}
#screenshot-modal .carousel .swiper-button-prev,#screenshot-modal .carousel .swiper-button-next{
  background-color:rgba(248,250,252,.7);
  background-image:none
}
#screenshot-modal .carousel .swiper-button-prev .c-icon, #screenshot-modal .carousel .swiper-button-next .c-icon{
  top:0;
  -webkit-flex:1 1 0%;
          flex:1 1 0%
}
#screenshot-modal .carousel .swiper-button-prev{
  left:0;
  margin-left:1rem;
  border-radius:0.25rem;
  padding-left:0.5rem;
  padding-right:0.5rem;
  padding-top:2rem;
  padding-bottom:2rem
}
#screenshot-modal .carousel .swiper-button-prev .c-icon{
  left:-2px
}
#screenshot-modal .carousel .swiper-button-next{
  right:0;
  margin-right:1rem;
  border-radius:0.25rem;
  padding-left:0.5rem;
  padding-right:0.5rem;
  padding-top:2rem;
  padding-bottom:2rem
}
#screenshot-modal .carousel .pagination-wrapper{
  position:absolute;
  bottom:0;
  z-index:10;
  display:-webkit-flex;
  display:flex;
  width:100%;
  padding-top:0;
  padding-bottom:0;
  bottom:40px
}
#screenshot-modal .carousel .pagination-wrapper .pagination-content{
  margin-left:auto;
  margin-right:auto;
  display:-webkit-flex;
  display:flex;
  max-width:20rem;
  -webkit-flex:1 1 0%;
          flex:1 1 0%;
  padding-left:2rem;
  padding-right:2rem
}
#screenshot-modal .carousel .pagination-wrapper .pagination-content .swiper-pagination{
  position:relative;
  display:-webkit-flex;
  display:flex;
  -webkit-flex:1 1 0%;
          flex:1 1 0%;
  border-radius:9999px;
  --tw-bg-opacity:1;
  background-color:rgba(228, 228, 231, var(--tw-bg-opacity));
  padding:0
}
#screenshot-modal .carousel .pagination-wrapper .pagination-content .swiper-pagination .swiper-pagination-bullet{
  -webkit-flex:1 1 0%;
          flex:1 1 0%;
  border-radius:9999px;
  --tw-bg-opacity:1;
  background-color:rgba(228, 228, 231, var(--tw-bg-opacity));
  height:8px
}
#screenshot-modal .carousel .pagination-wrapper .pagination-content .swiper-pagination .swiper-pagination-bullet.swiper-pagination-bullet-active{
  --tw-bg-opacity:1;
  background-color:rgba(39, 39, 42, var(--tw-bg-opacity))
}
@media(min-width: 700px){
.carousel .swiper-container .swiper-wrapper .swiper-slide .screenshot .swiper-zoom-container img{
    padding-left:100px;
    padding-right:100px
}
}
@media(min-height: 700px){
.carousel .swiper-container .swiper-wrapper .swiper-slide .screenshot .swiper-zoom-container img{
    padding-top:100px;
    padding-bottom:100px
}
}