.plugin-changelog{
  margin-bottom:2rem
}
.plugin-changelog.collapsed{
  position:relative;
  overflow:hidden;
  height:400px
}
.plugin-changelog.collapsed .more{
  position:absolute;
  bottom:0;
  display:block;
  width:100%;
  padding-bottom:1rem;
  padding-top:200px;
  background:#fff;
  background:-o-linear-gradient(top, rgba(255, 255, 255, 0) 0%, white 80%);
  background:linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, white 80%)
}
.plugin-changelog .more{
  text-align:center
}
.plugin-changelog .more a{
  display:inline-block
}
.plugin-changelog .changelog-release:last-child{
  border-bottom-width:0px
}